___CSS_LOADER_EXPORT___.push([module.id, `.product-card {
	background: var(--color-light);
	border-radius: var(--border-radius-large);
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
	overflow: hidden;
	transition:
		transform 0.2s,
		box-shadow 0.2s;
	height: 100%;
	display: flex;
	flex-direction: column;
}

.product-card:hover {
	box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

.product-card__image-wrapper {
	position: relative;
	width: 100%;
	height: 200px;
	overflow: hidden;
}

.product-card__image {
	width: 100%;
	height: 100%;
	object-fit: cover;
	display: block;
}

.product-card__content {
	padding: 16px;
	flex: 1;
	display: flex;
	flex-direction: column;
	gap: 12px;
}

.product-card__name {
	margin: 0;
	font-size: 1.1rem;
	font-weight: 600;
	color: var(--color-dark);
	line-height: 1.3;
}

.product-card__description {
	margin: 0;
	font-size: 0.9rem;
	color: #666;
	line-height: 1.4;
	flex: 1;
	display: -webkit-box;
	overflow: hidden;
}

.product-card__footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: auto;
}

.product-card__price {
	font-size: 1.2rem;
	font-weight: 700;
	color: var(--color-dark);
}

.product-card__add-btn {
	width: 40px;
	height: 40px;
	border: none;
	background: var(--color-accent);
	color: var(--color-light);
	border-radius: 50%;
	font-size: 1.5rem;
	font-weight: 600;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	transition:
		background-color 0.2s,
		transform 0.2s;
}

.product-card__add-btn:hover {
	background: var(--color-accent-hover);
}
